
@-webkit-keyframes badbounce {
0%,
  100% {
    -webkit-transform: translateY(0px);
}
10% {
    -webkit-transform: translateY(6px);
}
30% {
    -webkit-transform: translateY(-4px);
}
70% {
    -webkit-transform: translateY(3px);
}
90% {
    -webkit-transform: translateY(-2px);
}
}
@keyframes badbounce {
0%,
  100% {
    -webkit-transform: translateY(0px);
    transform: translateY(0px);
}
10% {
    -webkit-transform: translateY(6px);
    transform: translateY(6px);
}
30% {
    -webkit-transform: translateY(-4px);
    transform: translateY(-4px);
}
70% {
    -webkit-transform: translateY(3px);
    transform: translateY(3px);
}
90% {
    -webkit-transform: translateY(-2px);
    transform: translateY(-2px);
}
}
.menu-animate {
  -webkit-animation: badbounce 1s linear 1s;
  animation: badbounce 1s linear 1s;
}
.quick-menu {
  color: #fff;
  position: fixed;
  width: 60px;
  height: 60px;
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
  right: 30px;
}
.quick-menu > .menu {
  display: block;
  position: absolute;
  border-radius: 50% !important;
  width: 60px;
  height: 60px;
  text-align: center;
  -webkit-box-shadow: 0 3px 10px rgba(0, 0, 0, 0.23), 0 3px 10px rgba(0, 0, 0, 0.16);
          box-shadow: 0 3px 10px rgba(0, 0, 0, 0.23), 0 3px 10px rgba(0, 0, 0, 0.16);
  color: #fff;
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
}
.quick-menu > .menu .core-menu {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0px;
  top: 0px;
  width: 60px;
  height: 60px;
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
}
.quick-menu > .menu .core-menu .bar {
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
  width: 28px;
  height: 3px;
  background: #fff;
  position: absolute;
  top: 35%;
  margin-top: -1.5px;
  left: 16px;
  -webkit-transform-origin: 0% 50%;
  transform-origin: 0% 50%;
}
.quick-menu > .menu .core-menu .bar:before {
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
  content: '';
  width: 28px;
  height: 3px;
  background: #fff;
  position: absolute;
  margin-top: 30%;
  left: 0px;
  -webkit-transform-origin: 0% 50%;
  transform-origin: 0% 50%;
}
.quick-menu > .menu .core-menu .bar:after {
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
  content: '';
  width: 28px;
  height: 3px;
  background: #fff;
  position: absolute;
  margin-top: 60%;
  left: 0px;
  -webkit-transform-origin: 0% 50%;
  transform-origin: 0% 50%;
}
.quick-menu .sub-menu {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  position: absolute;
  width: 60px;
  height: 60px;
  font-size: 30px;
  text-align: center;
  border-radius: 50% !important;
  display: table;
}
.quick-menu .sub-menu a {
  text-decoration: none;
  display: inline-block;
  border-radius: 50% !important;
  width: 100%;
  height: 100%;
}
.quick-menu .sub-menu a i {
  outline: none;
}
.quick-menu .sub-menu a i:before {
  vertical-align: middle;
}
.quick-menu .sub-menu a:hover {
  cursor: pointer;
}
.quick-menu.active {
  -webkit-transform: rotate(0deg) !important;
  transform: rotate(0deg) !important;
}
.quick-menu.active .menu {
  -webkit-transform: scale(0.7);
  transform: scale(0.7);
}
.quick-menu.active .menu .bar {
  top: 50%;
  margin-top: -1.5px;
  left: 50%;
  margin-left: -12px;
  -webkit-transform-origin: 50% 50%;
  transform-origin: 50% 50%;
  -webkit-transform: rotate(405deg);
  transform: rotate(405deg);
}
.quick-menu.active .menu .bar:before {
  -webkit-transform-origin: 50% 50%;
  transform-origin: 50% 50%;
  -webkit-transform: rotate(-450deg);
  transform: rotate(-450deg);
  margin-top: 0px;
}
.quick-menu.active .menu .bar:after {
  opacity: 0;
}



